.GameWindow {
    width: 1500px;
    height: 90%;
    overflow: hidden;
}

.StatusPanel {
    display:flex;
    margin-left: 15px;
    height: 10%;
    align-items: center;
}

.StatusRow {
    display:flex;
    gap:5px;
    align-items: center;
}

.ThumbWithHover {
    position: relative;
}

.CardThumbnail {
    height: 80px;
    cursor: pointer;
}

.CardFull {
    height: 320px;
    z-index: 1;
    position: absolute;
}