.SidePanel {
    flex-basis: 30%;
    height: 100vh;
}

.DicePanel {
    position: relative;
    top: 500px;
}

.DiceImages {
    display:flex;
    justify-content: center;
    align-items: center;
    width:100%;
}

.DiceButtonPanel {
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width:100%;
    position: absolute;
    top: 100px;
}

.DiceButton {
    width: 90px;
    margin: 10px;
}

.CombatPanel {
    text-align: center;
    margin-top: 50px;
    position: relative;
}

.Card {
    width: 300px;
}